@font-face {
  font-family: 'wb-icons';
  src: url($wb-icon-path + "icomoon.woff") format("woff"), url($wb-icon-path + "icomoon.ttf") format("truetype"), url($wb-icon-path + "icomoon.svg#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="wb-icon"],
[class*=" wb-icon"],
.wb-icon {
  &:before {
      /* use !important to prevent issues with browser extensions that change fonts */
      font-family: 'wb-icons' !important;
      speak: none;
      font-style: normal;
      font-weight: normal;
      font-variant: normal;
      text-transform: none;
      line-height: inherit;
      display: inline-block;
      text-align: center;

      /* Better Font Rendering =========== */
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
  }
}

