//Transformations
.wb-rotate-180 {
    transform: rotate(180deg);
}

//Transitions
.wb-transition {
    transition: all $standardTransitionSpeed ease-in-out;
}

// Animations
.wb-animate-spin {
    animation: wb-spin $longTransitionSpeed infinite linear;
    display: inline-block;
}

@keyframes wb-spin {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(359deg);
        transform: rotate(359deg);
    }
}
