// RElIES ON .no-js & .js being on <html> or <body> if has JS, this is good practice

.wb-popdown {

  overflow: hidden;
  background: white;
  width: 100%;
  top: 100%;
  position: relative;
  z-index: 10;
  border: solid 2px $pi-color-darkneutral-dark;
  border-top: 0;
  box-shadow: 0 -4px 8px 1px rgba(0, 0, 0, 0.1);
  transition: opacity $standardTransitionSpeed ease-in-out;

  .js & {
    position: absolute;
    left: -200em;
    opacity: 0;
  }

  .wb-popdown--open &, &.wb-popdown--open {
    display: block;
    .js & {
      left: 0;
      opacity: 1;
    }
    &.wb-popdown--block {
      position: relative;
    }
  }

  &__inner {

    &__list {

      padding:0;
      margin:0;

      &__item {
        a,
        a:link,
        a:visited {
          text-decoration: none;
          display: block;
          color: $pi-color-greyscale-darkest;
          padding: .4em .6em;
          font-size: $font-size--s;
        }

        &--active{
          background-color:$pi-color-darkneutral-dark;
        }
      }
    }
  }

}
