$privacy-border: 1px solid $pi-color-greyscale-lighter;

.wb-privacy-accordion {
    max-width: 704px;
    &__header {
        -js-display: flex;
        @include flexbox();
        @include flex-direction(column);
        @include align-items(flex-start);
        margin: $base-spacing--base 0;
        @include mq($from: tablet) {
            @include flex-direction(row);
            @include justify-content(space-between);
            @include align-items(baseline);
        }
    }
    &__title {
        @include flex-grow(1);
        margin-bottom: $base-spacing--base;
        @extend .wb-strong;
        color: $pi-color-greyscale-darkest;
        @include mq($from: tablet) {
            margin-bottom: 0;
        }
    }
    &__download {
        display: none;
        &--display{
            display: inline-block;
        }
    }
    &__list {
        margin: 0;
        padding: 0;
        border-top: $privacy-border;
    }
}

.wb-privacy-accordion-item {
    border-bottom: $privacy-border;
    list-style: none;
    cursor: pointer;
    &__title {
        -js-display: flex;
        @include flexbox();
        @include justify-content(space-between);
        @include align-items(center);
        margin: $base-spacing--s 0;
        color: $pi-color-purple;
        &--text {
            display: inline-block;
            width: calc(100% - 30px);
        }
    }
    &__title{
        font-weight: bold;
    }
    &__content {
        display: none;
        color: $pi-color-greyscale-dark;
        .open & {
            display: block;
        }
    }

    &__chevron {
        font-size: 30px;
        .open & {
            @include rotate(180deg, 180deg);
            opacity: 0.5;
        }
    }

    &__content {
        ul {
            margin-left: $base-spacing--base;
            li {
                list-style: disc;
            }
        }
        li,
        p {
            margin-bottom: $base-spacing--base;
        }
    }
}