.wb-table {

  border-collapse: collapse;
  width: 100%;
  font-size: $font-size--s;
  background-color: $white;

  // Head
  &__head {
    display: table-header-group;
    background-color: $pi-color-greyscale-lightest;
    color: $pi-color-purple;
    border-bottom: 1px solid $pi-color-greyscale-light;
    text-align: left;
  }
  &__th {
    font-weight: normal;
  }

  // Body 
  &__body {
    display: table-row-group;
    background-color: $white;
  }
  &__row {
    flex-direction: row;
    flex-grow: 1;
    border-bottom: 1px solid $pi-color-greyscale-light;
    &--full-width {
      flex-wrap: wrap;
    }
    &--no-border {
      border: none;
    }
    &--active {
      background-color: $pi-color-neutral-darkest;
    }
  }
  &__cell {
    display: table-cell;
    padding: 25px 20px;
    flex-grow: 1;
    align-items: center;
    &--full-width {
      width: 100%;
      border-top: 1px solid $pi-color-greyscale-light;
    }
    &.wb-table__th {
      padding: 14px 20px;
    }
  }

  // Full Width Content
  &__full {
    &-content {
      margin: 20px 0;
      width: 100%;
      &--white {
        background-color: $white;
      }
    } 
  } 
  
  // Responsive 
  &__hide-xs {
    position: absolute;
    left: -200em;
    top: 0;
    @media #{$breakpoint-sm} {
      position: static;
      left: auto;
    }
  }
  &__hide-sm {
    position: absolute;
    left: -200em;
    top: 0;
    @media #{$breakpoint-md} {
      position: static;
      left: auto;
    }
  } 
  &__hide-md {
    position: absolute;
    left: -200em;
    top: 0;
    @media #{$breakpoint-lg} {
      position: static;
      left: auto;
    }
  } 

  // Pure Flex  / Divs
  // Allows more flexibility in layout / presentation
  &--flex {
    .wb-table__head,
    .wb-table__body,
    .wb-table__row, 
    .wb-table__cell {
      display: flex;
    }
    .wb-table__body {
      flex-direction: column;
    }

    &--col-4 {
      .wb-table__cell {
        width: 25%;
        &.wb-table__cell--full-width {
          width: 100%;
        }
      }
    }

    &--col-6 {
      .wb-table__cell {
        width: 16.667%;
        &.wb-table__cell--full-width {
          width: 100%;
        }
      }
    }
    
  }

  // Pure Divs
  &--divs {
    display: table;
    .wb-table__row {
      display: table-row;
    }
  }
}
