.wb-modal {
  display: block;
  position: fixed;
  z-index: 8001;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(51, 51, 51, 0.7);

  &.wb-modal--hidden {
    display: none;
  }

  .wb-modal-content {
    position: relative;
    width: calc(100% - (2 * #{$base-spacing--m}));
    max-width: $mobile--width;
    background-color: $pi-color-white;
    margin: $base-spacing--m auto;
    padding: $base-spacing--m;
    padding-top: calc(2 * #{$base-spacing--m});

    .wb-modal-content-close {
      position: absolute;
      top: $base-spacing--s;
      right: $base-spacing--s;
      line-height: $font-size--l;
      font-size: $font-size--l;
      font-weight: bold;
      color: $pi-color-greyscale-darkest;
    }
  }
}
