$checkbox-size: 21px;
$defaultIconSize: $font-size--l;

.wb-checkbox {
  // Default single checkbox style
  input[type='checkbox'] {
    opacity: 0;
    left: 0;
    position: absolute;

    &:focus ~ label {
      &:before {
        border-color: $checkbox-focus-label-color;
      }
    }
  }

  label {
    @extend .wb-soft-double--left;

    @extend .wb-flush--bottom;

    position: relative;
    display: inline-block;
    cursor: pointer;

    &:before,
    &:after {
      position: absolute;
      content: '';
      display: inline-block;
    }

    &:before {
      height: $checkbox-size;
      width: $checkbox-size;
      background: $checkbox-before-label-background;
      border: 1px solid $checkbox-before-label-border;
      border-radius: 3px;
      left: 0;
    }

    &:after {
      left: -1px;
      top: -1px;
    }
  }

  input[type='checkbox'] {
    & ~ label {
      &:after {
        content: none;
      }
    }

    &:checked {
      & ~ label {
        &:after {
          content: "\e957";
          font-family: 'wb-icons';
          font-size: $defaultIconSize;
          color: $checkbox-checked-label-color;
        }
      }
    }
  } //Default multiple checkbox style
  &-multiple {
    ul {
      @extend .wb-hard--base;

      @extend .wb-flush--base;
    }

    li {
      @extend .wb-flush--base;

      list-style: none;
      cursor: pointer;

      @include mq($until: tabletLarge) {
        width: 280px;
      }


      @include mq($from: tabletLarge) {
        width: 385px;
      }


      &:first-child {
        label {
          border-top: 1px solid $checkbox-multiple-list-label-border;
        }
      }
    }

    label {
      @extend .col-12;

      border-bottom: 1px solid $checkbox-multiple-label-border-bottom;
      border-left: 1px solid $checkbox-multiple-label-border-left;
      border-right: 1px solid $checkbox-multiple-label-border-right;
      padding: 11px $base-spacing--s $base-spacing--s 42px !important;

      &:before {
        background: $checkbox-multiple-label-before-background;
        left: 11px;
      }

      &:after {
        top: $base-spacing--s;
        left: $base-spacing--s;
      }
    }

    input[type='checkbox'] {
      &:checked {
        & ~ label {
          background: $checkbox-multiple-checked-label-background;
          border: 1px solid $checkbox-multiple-checked-label-border;
          transition: all .3s ease-in-out;
          margin-top: -1px;

          &:before {
            border: 1px solid $checkbox-multiple-checked-before-label-border;
          }
        }
      }
    }

    .wb-form-item__field {
      display: inline-block;
    } // Contained Dynamic Multiple Checkbox
    &__contained {
      @extend .wb-soft--base;

      border: 1px solid $checkbox-contained-border;

      @include mq($until: tabletLarge) {
        width: 280px;
      }


      @include mq($from: tabletLarge) {
        width: 385px;
      }


      li {
        @include mq($until: tabletLarge) {
          width: 240px;
        }


        @include mq($from: tabletLarge) {
          width: 345px;
        }
      }
    }

    &__dynamic {
      border-bottom: 1px solid $checkbox-dynamic-border-bottom;
      border-left: 1px solid $checkbox-dynamic-border-left;
      border-right: 1px solid $checkbox-dynamic-border-right;

      @extend .wb-soft--base;

      &-select {
        display: inline-block;
      }
    }

    &__clean {
      input[type='checkbox'] {
        &:checked {
          & ~ label {
            background: $checkbox-multiple-clean-checked-label-background;
            border: 1px solid $checkbox-multiple-clean-checked-label-border;
            transition: all .3s ease-in-out;
            margin-top: -1px;

            &:before {
              border: 1px solid $checkbox-multiple-clean-checked-before-label-border;
            }
          }
        }
      }
    }
  } // Single Checkbox Contained
  &-single {
    &__contained {
      @extend .wb-soft--base;

      border: 1px solid $checkbox-single-contained-border;

      @include mq($until: tabletLarge) {
        width: 280px;
      }


      @include mq($from: tabletLarge) {
        width: 525px;
      }


      label {
        &:before {
          background: $checkbox-single-contained-before-label-background;
        }
      }
    }
  } // Single Checkbox with label
  &-singlelabel {
    @extend .wb-push-half--top;
  } // Single Checkbox with error
  &-error {
    @include mq($until: tabletLarge) {
      width: 100%;
    }


    @include mq($from: tabletLarge) {
      width: 50%;
    }


    label {
      @extend .wb-soft-half--bottom;

      &:before {
        border: 1px solid $checkbox-error-before-label-border;
        background: $checkbox-error-before-label-background;
      }
    }

    span {
      @include font-size($font-size--xs, $line-height--xs);

      color: $checkbox-error-span-color;
      padding: 0 $base-spacing--xl;

      i {
        font-size: $defaultIconSize;
        vertical-align: middle;
        padding-right: 5px;
        position: absolute;
        left: 0;
      }
    }
  } // Checkbox List
  &-list {
    ul {
      @extend .wb-hard--base;

      @extend .wb-flush--base;
    }

    li {
      @extend .wb-soft-half--bottom;

      @extend .wb-flush--base;

      width: 385px;
      list-style: none;
      cursor: pointer;
    }
  } // Contained checkbox hide
  &__contained {
    @extend .wb-soft--base;

    border: 1px solid $checkbox-contained-border;

    &-subtext {
      @include font-size($font-size--xs, $line-height--xs);
    }

    @include mq($until: tabletLarge) {
      width: 280px;

      &-left {
        padding-bottom: $base-spacing--m;
      }
    }


    @include mq($from: tabletLarge) {
      width: 525px;

      &-left,
      &-right {
        display: inline-block;
      }

      &-right {
        float: right;
      }
    }


    &-right {
      label {
        color: $checkbox-contained-right-label-color;
        font-weight: bold;
      }
    }

    h5,
    p {
      @extend .wb-flush--base;
    }

    h5 {
      @extend .wb-heading--h5;

      color: $checkbox-contained-h5-color;
    }

    .wb-checkbox {
      label {
        @extend .wb-hard--left;

        padding-right: 35px;

        &:before,
        &:after {
          right: 0;
          left: inherit;
          font-weight: normal;
        }
      }
    }
  }

  &__white {
    label {
      &:before {
        background: $checkbox-white-before-label-background;
      }
    }
  }

  &__large {
    label {
      padding-left: 45px !important;

      &:before {
        width: $base-spacing--xl;
        height: $base-spacing--xl;
        font-size: 40px;
        top: -3px;
      }
    }

    input[type='checkbox'] {
      &:checked {
        & ~ label {
          &:after {
            font-size: 30px;
            top: 2px;
            left: 1px;
          }
        }
      }
    }
  }
}

.singlelabel {
  color: $checkbox-single-label-color;
}
