$wb-header-height: 90px;

.wb-header {
    @extend .clearfix;

    background: $white;
    font-size: $font-size--base;
    position: relative;

    &__toggle-mobile-nav {
        position: absolute;
        right: 20px;
        top: 20px;
        cursor: pointer;
        margin: auto;
        color: $pi-color-secondary-mid;

        &.active {
            color: $header-mobile-toggle-active;
        }

        .wb-burger-bar {
            width: 44px;
            margin-bottom: 29px;
            content: '';
            left: 0;
            height: 4px;
            border-radius: 4px;
            background: $header-mobile-burger-bar;
            box-shadow: 0 4px transparent, 0 11px $header-mobile-box-shadow, 0 16px transparent, 0 22px $header-mobile-box-shadow;
        }

        @extend .sm-hide,
        .md-hide,
        .lg-hide,
        .wb-button-reset;

        span {
            display: block;
            text-align: center;
            margin-top: 2px;
            font-size: $font-size--base;
        }
    }

    &__primary-navigation {
        @extend .clearfix, .wb-container;

        position: relative;

        & > a {
            display: block;
        }

        .wb-header__logo-container {
            border-bottom: 1px solid $pi-color-darkneutral-light;
            height: $wb-header-height;
            padding: 0 20px;

            @extend .border-box;

            @media #{$breakpoint-sm} {
                border: none;
                height: auto;
                padding: 0;
            }

            a {
                display: block;
                width: 71px;
                overflow: hidden;

                @media #{$breakpoint-sm} {
                    width: auto;
                    display: inline;
                }

                i {
                    padding: 9px 0;
                    font-size: 70px;
                    float: left;
                }
            }
        }

        & > ul {
            @extend .clearfix,
            .p0,
            .m0,
            .list-reset;

            & > li {
                @media #{$breakpoint-sm} {
                    max-width: 130px;
                    height: $wb-header-height;
                    line-height: $wb-header-height;
                }

                @extend .sm-col,
                .sm-col-4,
                .center;

                .wb-header__top-nav-label {
                    cursor: pointer;
                    vertical-align: middle;
                    line-height: 18px;
                    display: block;
                    padding: 10px 20px;
                    text-align: left;
                    border-bottom: 1px solid $pi-color-darkneutral-light;

                    @media #{$breakpoint-sm} {
                        padding: 29px 0;
                        display: inline-block;
                        text-align: center;
                        border: none;
                        width: 100%;
                    }
                }

                &.active .top-nav-label, &:hover .top-nav-label {
                    @extend %wb-base-font-bold;
                }

                ul {
                    &.active {
                        max-height: 350px;
                        transition: max-height $standardTransitionSpeed ease-in-out;
                    }

                    margin: 0;
                    padding: 0;
                    overflow: hidden;
                    text-align: left;
                    background: $pi-color-darkneutral-light;
                    color: $header-list-color-active;
                    max-height: 0;
                    transition: none;
                    overflow: hidden;

                    @extend .sm-hide,
                    .md-hide,
                    .lg-hide;

                    li {
                        margin: 0;
                        list-style: none;
                        border-bottom: 1px solid $pi-color-darkneutral-light;
                        text-align: left;

                        a {
                            padding: 10px 20px;
                            display: block;
                        }
                    }
                }
            }
        }
    }

    &__secondary-navigation {
        color: $header-secondary-navigation-color-active;
        background: $pi-color-darkneutral-light;
        position: absolute;
        top: 90px;
        left: 0;
        right: 0;
        transition: $standardTransitionSpeed ease-in-out opacity, $standardTransitionSpeed ease-in-out top;
        z-index: 1;
        min-height: 42px;

        @extend .p0;

        margin: 0 auto;
        text-align: center;
        z-index: 1;

        li {
            display: inline-block;
            vertical-align: middle;
            text-align: center;

            a {
                padding: 10px;
                display: block;

                &:hover {
                    @extend %wb-base-font-bold;

                    text-decoration: none;
                }

                &::after {
                    display: block;
                    content: attr(title);

                    @extend %wb-base-font-bold;

                    height: 1px;
                    color: transparent;
                    overflow: hidden;
                    visibility: hidden;
                }
            }
        }
    }
}
