.wb-form-item {
  .wb-form-item__field {
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      cursor: pointer;
      @extend %wb-form-item--input-style;
    }
    select:disabled {
      background-color: $pi-color-greyscale-lightest;
    }
  }
  &--clean {
    .wb-form-item__field {
      select,
      .wb-btn {
        @extend %wb-form-item--input-clean !optional;
      }
    }
  }
}

.wb-native-dropdown-offset-flag {
  select {
    padding-left: 40px !important;
  }
}

.wb-native-dropdown--flag {
  width: 24px;
  height: 24px;
  left: $base-spacing--s/2;
  position: absolute;
  top: 50%;
  @include transform(translate(0, -50%));
  @include mq($from: tablet) {
    left: $base-spacing--s;
  }
}
