$accordion-default-height: 40px;
$accordion-toggle-width-small: 280px;
$accordion-toggle-width-large: 300px;

.wb-accordion {
    @extend %wb-base-font-regular;

    @include font-size($font-size--base, $line-height--xs);

    width: 100%;
    font-weight: bold;
    color: $accordion-color;
    background-color: $accordion-background;
    border: 1px solid $accordion-border;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    height: $accordion-default-height;
    outline: none;
    cursor: pointer;
    padding: $base-spacing--s;
    -webkit-appearance: none;

    [class^="wb-icon"],
    [class*=" wb-icon"],
    .wb-icon {
        &:before {
            font-size: $font-size--l;
        }
    }

    &__label {
        vertical-align: top;

        &:hover {
            text-decoration: underline;
        }
    }

    &__open {
        border-bottom: 0;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        padding-bottom: $base-spacing--s;
        padding-top: $base-spacing--s;

        .wb-icon_arrow-up:before {
            color: $accordion-open-icon-color;
        }

        .wb-accordion__label {
            text-align: center;
            display: inline-block;
        }

        & + .wb-accordion__container {
            @extend .wb-accordion;

            @include font-size($font-size--s, $line-height--s);

            border-top: 0;
            padding: 0 $base-spacing--m $base-spacing--m $base-spacing--m;
            height: auto;
            color: $accordion-open-container-color;
            font-weight: normal;
            display: block;
        }
    }

    &__room-details {
        height: auto;
        padding: $base-spacing--m;

        .wb-icon_arrow-up,
        .wb-icon_arrow-down {
            position: relative;
            float: right;
            font-size: $font-size--l;
        }

        &-left,
        &-right {
            display: block;
            text-align: left;
        }

        &-left {
            margin-bottom: $base-spacing--s;

            @include mq($from: tabletLarge) {
                float: right;
                text-align: right;
                width: 35%;
                display: inline-block;
                margin-bottom: 0;
            }
        }

        &-right {
            @include mq($from: tabletLarge) {
                float: left;
                text-align: left;
                width: 60%;
                display: inline-block;
            }
        }
    }

    &__contained {
        width: 100%;

        & + .wb-accordion__container {
            width: 100%;
        }
    }

    &__toggle {
        border-top-right-radius: 5px;
        border-top-left-radius: 5px;
        width: $accordion-toggle-width-small;
        height: $accordion-default-height;
        padding: $base-spacing--s;

        @include mq($from: tablet) {
            width: $accordion-toggle-width-large;
        }


        &:hover {
            background-color: $accordion-toggle-hover-background;
            border: 1px solid $accordion-toggle-hover-border;
        }

        .wb-icon_arrow-up:before {
            color: $accordion-toggle-icon-color;
        }
    }
}
