.wb-tooltip {
    background-color: $tooltip-background;
    display: block;
    padding: 10px;
    padding-left: 44px;

    @include font-size($font-size--xs, $line-height--xxs);

    position: relative;
    min-height: 45px;
    width: 240px;

    svg {
        margin-left: -3px;
        margin-top: -8px;
        height: 30px;
        width: 28px;

        path[fill] {
            fill: $tooltip-svg-path;
        }
    }

    @media #{$breakpoint-md} {
        margin-left: 20px;
        padding-left: 46px;
    }

    .wb-icon {
        position: absolute;
        font-size: 24px;
        color: $tooltip-icon-color;
        left: 12px;
        top: 15px;

        @media #{$breakpoint-md} {
            left: 13px;
            top: 16px;
        }
    }

    &:before {
        position: absolute;
        content: '';
        display: inline-block;
        width: 0;
        height: 0;
        left: 10px;
        top: -8px;
        border-left: 10px solid $tooltip-before-border-left;
        border-right: 10px solid $tooltip-before-border-right;
        border-bottom: 8px solid $tooltip-before-border-bottom;

        @media #{$breakpoint-md} {
            left: -20px;
            top: 10px;
            border-top: 10px solid $tooltip-before-media-border-top;
            border-right: 10px solid $tooltip-before-media-border-right;
            border-bottom: 8px solid $tooltip-before-media-border-bottom;
        }
    } //Theme overrides
    .wb-form-item--clean & {
        @extend %wb-tooltip--clean !optional;
    }
}
