%wb-form-item--input-clean {
  background-color: $pi-color-white;
  border-color: $pi-color-greyscale-light;
  font-style: normal;
  font-weight: normal;

  &.ng-invalid.ng-dirty, &.error-field {
    background-color: $pi-color-neutral-dark;
  }

  @include placeholder {
    font-style: normal;
  }
}
