/*
  Fonts are setup via Style Linking, instead of multiple font-familys for consistancy and using the same font family.

  PremierInn Sans IS one font family, but has different fonts in different situations.
  Eg. We should never "bold" PI-Sans Regualar.... it should get PI-Sans_Bold font.

  Chrome (and others?) are smart enough to only download the font-versions needed

  Zeplin: https://app.zeplin.io/project/59955e423afdaa297f4b5866/screen/5996cdd3f434e52d735018ab
  Font Linking: https://www.smashingmagazine.com/2013/02/setting-weights-and-styles-at-font-face-declaration/

*/

/* ***** PI Sans Light ***** */
/* PI Sans Light - Regular */
@font-face {
  font-family: 'PremierInn_Sans';
  src:  url($wb-font-path + 'PremierInnSansWeb-Light.woff2') format('woff2'),
        url($wb-font-path + 'PremierInnSansWeb-Light.woff') format('woff'),
        url($wb-font-path + 'PremierInnSansWeb-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}
/* PI Sans Light - Italic */
@font-face {
  font-family: 'PremierInn_Sans';
  src:  url($wb-font-path + 'PremierInnSansWeb-LightItalic.woff2') format('woff2'),
        url($wb-font-path + 'PremierInnSansWeb-LightItalic.woff') format('woff'),
        url($wb-font-path + 'PremierInnSansWeb-LightItalic.ttf') format('truetype');
  font-weight: 300;
  font-style: italic;
}

/* ***** PI Sans Regular ***** */
/* PI Sans Regular - Regular */
@font-face {
  font-family: 'PremierInn_Sans';
  src: url($wb-font-path + 'PremierInnSansWeb-Regular.woff2') format('woff2'),
       url($wb-font-path + 'PremierInnSansWeb-Regular.woff') format('woff'),
       url($wb-font-path + 'PremierInnSansWeb-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
/* PI Sans Regular - Italic */
@font-face {
  font-family: 'PremierInn_Sans';
  src: url($wb-font-path + 'PremierInnSansWeb-Italic.woff2') format('woff2'),
       url($wb-font-path + 'PremierInnSansWeb-Italic.woff') format('woff'),
       url($wb-font-path + 'PremierInnSansWeb-Italic.ttf') format('truetype');
  font-weight: normal;
  font-style: italic;
}

/* ***** PI Sans Bold ***** */
/* PI Sans Bold - Regular */
@font-face {
    font-family: 'PremierInn_Sans';
    src: url($wb-font-path + 'PremierInnSansWeb-Bold.woff2') format('woff2'),
         url($wb-font-path + 'PremierInnSansWeb-Bold.woff') format('woff'),
         url($wb-font-path + 'PremierInnSansWeb-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;

}
/* PI Sans Bold - Italic */
@font-face {
    font-family: 'PremierInn_Sans';
    src: url($wb-font-path + 'PremierInnSansWeb-BoldItalic.woff2') format('woff2'),
         url($wb-font-path + 'PremierInnSansWeb-BoldItalic.woff') format('woff'),
         url($wb-font-path + 'PremierInnSansWeb-BoldItalic.ttf') format('truetype');
    font-weight: bold;
    font-style: italic;
}
