.wb-btn {
  -webkit-appearance: none;
  font-size: $font-size--m;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  text-decoration: none;
  color: $button-color;
  background-color: $button-background;
  border: 2px solid $button-border;
  line-height: #{$buttonLineHeight - $buttonBorderWidth * 2};

  @include buttonTransition;

  padding: 5px 20px;

  @include border-radius($standardBorderRadius);

  outline: none;
  font-weight: bold;
  display: inline-block;

  @extend %wb-base-font-regular;

  &:focus {
    @extend .wb-underline;
  }

  &.wb-btn--small {
    line-height: 21px;
    font-size: $font-size--base;
  }

  &.wb-btn--block {
    display: block;
  }

  &.wb-btn--full-width {
    width: 100%;
  }

  &.wb-btn--disabled {
    background-color: $button-disabled-background;
    color: $button-disabled-color;
    border-color: $button-disabled-border;

    &:hover,
    &:active,
    &:focus {
      background-color: $button-disabled-focus-background;
      text-decoration: none;
      cursor: default;
    }
  }

  &.wb-btn--primary {
    color: $button-primary-color;
    background-color: $button-primary-background;
    border-color: $button-primary-border;

    &:hover, &.wb-btn--active, &:active, &:focus {
      background-color: $button-primary-state-background;
      border-color: $button-primary-state-border;
    }
  }

  &.wb-btn--secondary {
    color: $button-secondary-color;
    background-color: $button-secondary-background;
    border-color: $button-secondary-border;

    &:hover, &.wb-btn--active, &:active, &:focus {
      color: $button-secondary-state-color;
      background-color: $button-secondary-state-background;
      border-color: $button-secondary-state-border;
    }
  }

  &.wb-btn--tertiary {
    background-color: $button-tertiary-background;
    color: $button-tertiary-color;
    border-color: $button-tertiary-border;

    &:hover, &.wb-btn--active, &:active, &:focus {
      background-color: $button-tertiary-state-background;
    }
  }

  &.wb-btn--quaternary {
    background-color: $button-quaternary-background;
    color: $button-quaternary-color;
    border: $buttonBorderWidth solid $button-quaternary-border;

    &:hover, &.wb-btn--active, &:active, &:focus {
      color: $button-quaternary-state-color;
      background-color: $button-quaternary-state-background;
      border-color: $button-quaternary-state-border;
    }
  }

  &.wb-btn--quaternary-light {
    background-color: $button-quaternary-light-background;
    color: $button-quaternary-light-color;
    border: $buttonBorderWidth solid $button-quaternary-light-border;

    &:hover, &.wb-btn--active, &:active, &:focus {
      color: $button-quaternary-light-state-color;
      background-color: $button-quaternary-light-state-background;
      border-color: $button-quaternary-light-state-border;
    }
  }

  &.wb-btn--tertiary-light {
    background-color: $button-tertiary-light-background;
    color: $button-tertiary-light-color;
    border: $buttonBorderWidth solid $button-tertiary-light-border;

    &:hover, &.wb-btn--active, &:active, &:focus {
      color: $button-tertiary-light-state-color;
      background-color: $button-tertiary-light-state-background;
      border-color: $button-tertiary-light-state-border;
    }
  }

  &.wb-btn--square {
    border-radius: 0px;
    line-height: 24px;
  }

  &.wb-btn-circle {
    &--small {
      padding: 0px;
      height: 30px;
      width: 30px;
      border-radius: 15px;
    }

    &--medium {
      padding: 0px;
      height: 40px;
      width: 40px;
      border-radius: 20px;
    }

    &--large {
      padding: 0px;
      height: 60px;
      width: 60px;
      border-radius: 30px;
    }
  }

  i, .wb-icon {
    vertical-align: middle;
    margin-top: -3px;
    display: inline-block;
  }
}

.wb-text-btn {
  -webkit-appearance: none;
  background-color: transparent;
  font-size: $font-size--s;
  text-align: left;
  cursor: pointer;
  color: $pi-color-secondary-darkest;
  border: none;
  line-height: normal;
  padding: 5px 0;

  &:focus {
    text-decoration: underline;
    outline: none;
  }

  &.wb-text-btn--red {
    text-decoration: none;
    color: $pi-color-accent-coral;

    &:focus {
      text-decoration: underline;
    }
  }

  &.wb-text-btn--highlight {
    font-size: $font-size--base;
    font-weight: bold;
    text-decoration: underline;
  }

  &.wb-text-btn--underline {
    text-decoration: underline;
  }
}
