%wb-radio--yellow {

  .wb-radio__label {
    margin: 4px 0;
  }

  &:last-child {
    .wb-radio__label__inner {
      border-bottom: 1px solid $pi-color-neutral-mid;
    }
  }

  .wb-radio__label__inner {
    background: $pi-color-neutral-mid;
    border: 1px solid $pi-color-neutral-mid;

    &:before {
      background-color: $white;
    }
  }

  input[type=radio] {
    // Outline
    &:focus + .wb-radio__label__inner {
      box-shadow: 0 0 0 1px $pi-color-btn-primary;
    }
    // Background color highlighted 
    &:checked + .wb-radio__label__inner {
      background: $pi-color-accent-orange-lightest;
      border-color: $pi-color-btn-primary;
    }
  }
  
}

