.form-item-msg {
    font-size: $font-size--xs;

    @extend %wb-base-font-bold;

    display: block;
    padding-top: 5px;

    &--error {
        color: $validation-error-color;
    }

    &--info {
        color: $pi-color-accent-teal;
    }

    &__error-icon {
        position: absolute;
        right: -32px;
        top: 6px;
        font-size: $font-size--l;
        color: $pi-color-accent-darkred;
    }

    &__info-icon {
        position: absolute;
        right: -32px;
        top: 6px;
        font-size: $font-size--l;
        color: $pi-color-accent-aqua;
    }
}
