/* Fonts */

%wb-base-font-light {
    font-weight: 300;
    font-style: normal;
    font-family: $base-font;
}

%wb-base-font-light-italic {
    font-weight: 300;
    font-style: italic;
    font-family: $base-font;
}

%wb-base-font-regular {
    font-weight: normal;
    font-style: normal;
    font-family: $base-font;
}

%wb-base-font-regular-italic {
    font-weight: normal;
    font-style: italic;
    font-family: $base-font;
}

%wb-base-font-bold {
    font-weight: bold;
    font-style: normal;
    font-family: $base-font;
}

%wb-base-font-bold-italic {
    font-weight: bold;
    font-style: italic;
    font-family: $base-font;
}

/* Links / a tags */

@mixin wb-a($color, $color-highlight: $color) {
    color: $color;
    -webkit-tap-highlight-color: $color-highlight;
    cursor: pointer;
    text-decoration: underline;

    &:link {
        color: $color;
        -webkit-tap-highlight-color: $color-highlight;
    }

    /* styling of links in focus (tabbing) highlight child icons and images not the anchor */
    &:focus {
        text-decoration: none;
        outline: solid 1px;
    }

    &[class*=" wb-icon"],
    &[class^="wb-icon"] {
        position: relative;
        margin-left: 20px;

        &:before {
            position: absolute;
            left: -20px;
            vertical-align: bottom;
            line-height: 23px;
        }
    }
}

.wb-a {
    > i[class*="wb-icon_link-round"],
    > i[class^="wb-icon_link-round"] {
        &:before {
            background: $link-icon-round-background;
            color: $link-icon-round-color;
            border-radius: 50%;
            font-size: $font-size--m;
            margin-right: $base-spacing--s;
            vertical-align: middle;
            width: $base-spacing--m;
            height: $base-spacing--m;
        }
    }

    > i[class*="wb-icon_link-round-open"],
    > i[class^="wb-icon_link-round-open"] {
        &:before {
            background: $link-icon-round-open-background;
        }
    }

    > i[class*="wb-icon_link-clean"],
    > i[class^="wb-icon_link-clean"] {
        &:before {
            color: $link-icon-clean-color;
            font-size: $font-size--l;
            vertical-align: middle;
        }
    }

    > i[class*="wb-icon_link-clean-open"],
    > i[class^="wb-icon_link-clean-open"] {
        &:before {
            color: $link-icon-clean-open-color;
        }
    }

    @include wb-a($link-color, $link-highlight-color);

    &.wb-a--danger {
        @include wb-a($link-danger-color);
    }
}

/* Lists */

ul {
    li {
        i {
            font-size: $font-size--l;

            &.wb-icon_tick {
                font-size: medium;
                margin-right: 4px;
            }
        }
    }
}

/* Alignment */

.align {
    &--left {
        text-align: left;
    }

    &--center {
        text-align: center;
    }

    &--right {
        text-align: right;
    }
}

/* Typography */

// General parent class for descoping element styles and easy application in a project
.font-size {
    &--xs {
        @include font-size($font-size--xs, $line-height--xxs);
    }

    &--s {
        @include font-size($font-size--s, $line-height--s);
    }

    &--base {
        @include font-size($font-size--base, $line-height--xs);
    }

    &--m {
        @include font-size($font-size--m, $line-height--m);
    }

    &--l {
        @include font-size($font-size--l, $line-height--l);
    }

    &--xl {
        @include font-size($font-size--xl, $line-height--xl);
    }

    &--xxl {
        @include font-size($font-size--xxl, $line-height--xxl);
    }

    &--xxxl {
        @include font-size($font-size--xxxl, $line-height--xxxl);
    }
}

.wb-typography {
    // SHOULD - Just move .wb-typography to body tag
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    @extend %wb-base-font-regular;

    ::selection {
        @extend .wb-selection;
    }

    &--accent-primary {
        color: $pi-color-secondary-darkest;
    }

    &--label {
        color: $pi-color-greyscale-mid;

        @extend %wb-base-font-bold;
    }
}

.wb-price {
    &--xlarge {
        @extend %wb-base-font-bold;

        font-size: $price-xlarge;

        .wb-price__decimal {
            font-size: $price-large;
        }
    }

    &--large {
        @extend %wb-base-font-bold;

        font-size: $price-large;

        .wb-price__decimal {
            font-size: $price-mid;
        }
    }

    &--mid {
        @extend %wb-base-font-bold;

        font-size: $price-mid;

        .wb-price__decimal {
            font-size: $price-small;
        }
    }

    &--small {
        @extend %wb-base-font-bold;

        font-size: $price-small;

        .wb-price__decimal {
            font-size: $price-xsmall;
        }
    }
}

/* --- Font Modifiers --- */

.wb-selection {
    color: $pi-color-white;
    background: $pi-color-secondary-mid;
}

.wb-strong {
    font-weight: bold;
}

.wb-underline {
    text-decoration: underline;
}

.wb-strikethrough {
    text-decoration: line-through;
    color: $pi-color-greyscale-mid;
}

/* Body Text  */

//@todo: these are too specific to BB project, needs to be made more generic
.page-content {
    p {
        line-height: $line-height--s;
        color: $pi-color-greyscale-darkest;
        font-size: $font-size--s;

        a,
        a:link {
            color: $pi-color-secondary-darkest;

            & > strong {
                font-weight: bold;

                @extend .wb-underline;
            }
        }
    }

  /* Targeting only pi-form as it was breaking other pages
        TODO: make this global
*/
    .pi-form {
        ul {
            line-height: $line-height--s;
            color: $pi-color-greyscale-darkest;
            font-size: $font-size--s;

            a,
            a:link {
                color: $pi-color-secondary-darkest;

                & > strong.wb-strong {
                    font-weight: bold;

                    @extend .wb-underline;
                }
            }
        }
    }
}

.action_links,
.site-header {
    a:focus {
        outline: none;

        img {
            outline: solid 1px $pi-color-purple;
        }
    }
}

.site-header {
    .sub-navigation {
        a:focus {
            text-decoration: none;
        }
    }
}

ul.bullet-list {
    list-style: disc outside;
    padding-left: 15px;

    li {
        text-indent: -5px;
        list-style: inherit;
    }
}

.wb-section {
    padding-top: 1em;
    padding-bottom: 1em;
    color: $pi-color-greyscale-darkest;
    border-bottom: solid 2px $pi-color-greyscale-light;

    @extend .wb-container;

    .wb-a.right {
        font-size: $font-size--s;
    }

    &--no-border {
        border-bottom: none;
    }

    &.wb-section--pt0 {
        @extend .pt0;
    }

    &.wb-section--pb0 {
        @extend .pb0;
    }

    &.wb-section--px0 {
        @extend .px0;
    }
}

