.wb-list {
    &--white {
        a,
        a:link {
            color: $pi-color-white;
        }

        i {
          color: $pi-color-white;
        }
    }
    a {
        display: inline-block;
        padding: 8px 0;
        position: relative;
        @media #{$breakpoint-sm} {
            padding: 2px 0;
        }
    }
    &--chevron {
        list-style: none;

        i {
          vertical-align: middle;
          font-size: $font-size--l;
        }
    }
}