$datepicker-width: 300px;
$line-height-datepicker-title: 32px;

.wb-datepicker {
    @extend .wb-typography;
    width: 100%;
    @media #{$breakpoint-xs} {
        width: $datepicker-width;
    }
    background-color: $white;
    @include box-shadow(0 -4px 8px 1px rgba(0, 0, 0, 0.1));
    border: solid 2px $pi-color-darkneutral-dark;
    .wb-datepicker-title {
        position: relative;
        text-align: center;

        .wb-datepicker-title-label {
            display: inline-block;
            position: relative;
            overflow: hidden;
            margin: 0;
            padding: 0 3px;
            font-size: $font-size--s;
            line-height: $line-height-datepicker-title;
            font-weight: bold;
            background-color: $white;
            select {
                cursor: pointer;
                position: absolute;
                margin: 0;
                left: 0;
                top: 5px;
                opacity: 0;
            }
        }

        button {
            line-height: $line-height-datepicker-title;
        }

        button:before {
            color: $pi-color-purple;
        }

        .wb-datepicker-prev-next {
            @extend .wb-icon,
            .wb-button-reset,
            .wb-datepicker-prev-next;
            position: absolute;
            color: transparent;
            cursor: pointer;
            width: 21px;
            height: 21px;
            font-size: $font-size--l;
            text-align: center;
            overflow: hidden;
            top: 5px;
            &:before {
                color: $pi-color-secondary-darkest;
			}
			&.is-disabled {
                display: none;
            }
        }

        .wb-datepicker-prev, .wb-datepicker-next{
            position: absolute;
            border: none;
            height: 28px;
            width: 30px;
            background: none;
            color: transparent;
            font-family: "wb-icons";
            font-size: $font-size--l;
        }

        .wb-datepicker-prev {
            left: 2px;
            @extend .wb-icon_arrow-left;
        }

        .wb-datepicker-next {
            right: 2px;
            @extend .wb-icon_arrow-right;
        }
    }
    table {
        width: 100%;
        thead {
            background: $pi-color-neutral-light;
            th {
                color: $pi-color-secondary-darkest;
                &:nth-child(6),
                &:nth-child(7) {
                    color: lighten($pi-color-secondary-darkest, 15%);
                }
            }
            abbr {
                text-decoration: none;
                font-size: $font-size--xs;
                display: block;
                @extend .py1;
            }
        }
        tbody {
            td {
                text-align: center;
                padding: 0;
                &.wb-datepicker-inactive {
                    button {
                        color: $pi-color-greyscale-light;
                    }
				}
				&.wb-datepicker-disabled {
					button {  
					  color: $pi-color-greyscale-light;
					  cursor: not-allowed;
					}
				}
                &.wb-datepicker-selected {
                    button {
                        background-color: $pi-color-darkneutral-light;
                        border: 1px solid $pi-color-darkneutral-dark;
                        width: 30px;
                        height: 30px;
                        padding: 0;
                        @include border-radius(100%);
                    }
                }
                &.wb-datepicker-today {
                    button {
                        border: 1px solid $pi-color-darkneutral-light;
                        width: 30px;
                        height: 30px;
                        padding: 0;
                        @include border-radius(100%);
                    }
                }
                button {
                    color: $pi-color-greyscale-dark;
                    display: inline;
                    text-align: center;
                    outline: 0;
                    border: none;
                    background: none;
                    font-size: $font-size--s;
                    @extend .py2,
                    .px1;
                    font-weight: 500;
                    cursor: pointer;
                    display: inline-block;
                    width: 100%;
                }
            }
        }
        tr {
            td,
            th {
                &:nth-child(6) {
                    border-left: 1px solid $pi-color-neutral-darkest;
                }
            }
        }
    }
}

//PIKA THIRD PARTY INTEGRATION
.pika-single {
    @extend .wb-datepicker;
    .pika-title {
        @extend .wb-datepicker-title;
        .pika-label {
            @extend .wb-datepicker-title-label;
		}
		.pika-prev, .pika-next {
			&.is-disabled {
			  @extend .wb-datepicker-prev-next.is-disabled
			}
		}
        .pika-prev {
            @extend .wb-datepicker-prev;
        }
        .pika-next {
            @extend .wb-datepicker-next;
        }
    }
    .is-empty {
        @extend .wb-datepicker-inactive;
    }
    .is-selected {
        @extend .wb-datepicker-selected;
    }
    .is-today {
        @extend .wb-datepicker-today;
    }
    .is-outside-current-month {
        @extend .wb-datepicker-inactive;
    }
    &.is-hidden {
        @extend .display-none;
	}
	.is-disabled {
		@extend .wb-datepicker-disabled;    
	}
	td:hover {
		&:not(.is-disabled) {
			@extend .wb-datepicker-selected;
		}
	}
}
