.wb-notification {
    box-sizing: border-box;
    padding: $base-spacing--s;
    position: relative;
    color: $notification-color;

    @include font-size($font-size--s, $line-height--s);

    width: auto;

    a:visited,
    a:link {
        text-decoration: underline;
    }

    &-header {
        @extend .wb-strong;

        margin: 0 0 0 35px;
        color: $notification-header-color;
        font-size: $font-size--s;
    }

    &-icon {
        &:before {
            position: absolute;
            left: 12px;
            top: 10px;
            font-size: $notificationicon;
        }
    }

    &-text {
        vertical-align: top;
        margin: 0 0 0 35px;
    }

    &__info {
        background-color: $notification-info-background;
        border: solid 1px $notification-info-border;

        [class*="wb-icon"] {
            color: $notification-info-icon-color;
        }
    }

    &__unhousedinfo {
        .wb-notification-header,
        .wb-notifcation-text {
            color: $notification-unhousedinfo-color;
        }

        &-center {
            text-align: center;

            .wb-notification-header {
                color: $notification-unhousedinfo-header-color;

                &:before {
                    @extend .wb-icon_disc-info-new;

                    margin-right: 10px;
                    color: $notification-unhousedinfo-icon-color;
                }
            }
        }
    }

    &__alert {
        background-color: $notification-alert-background;

        > a {
            margin-left: 35px;
        }

        .wb-icon_disc-info-new {
            color: $notification-alert-icon-color;
        }
    }

    &__unhousedalert {
        @extend .wb-strong;

        color: $notification-unhousedalert-color;

        .wb-notification-header {
            color: $notification-unhousedalert-header-color;

            & + .wb-notification-text {
                font-weight: normal;
                color: $notification-unhousedalert-header-color;
                margin: 10px 0 0 5px;
            }
        }
    }

    &__sticky {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 8002;
    }

    &__erroralert {
        background-color: $notification-erroralert-background;
        border: solid 1px $notification-erroralert-border;
        color: $notification-erroralert-color;
    }

    &__highlight {
        border: solid 1px $notification-highlight-border;

        .wb-icon_star-filled {
            color: $notification-highlight-icon-color;
        }
    }

    &__success {
        background-color: $notification-success-background;
        border: solid 1px $notification-success-border;

        .wb-icon_disc-checked-fill {
            color: $notification-success-icon-checked-color;
        }

        .wb-icon_cross:before {
            font-size: $font-size--base;
            color: $notification-success-icon-cross-color;
            position: absolute;
            left: auto;
            right: $base-spacing--s;
            top: 5px;
        }

        &-unhoused {
            border: none;

            .wb-icon_disc-checked-fill {
                color: $notification-success-unhoused-checked-icon-color;
            }
        }
    }

    &__success-bold {
      background-color: $notification-bold-success-background;

      .wb-icon_tick:before {
        color: $notification-bold-success-icon-tick-color;
      }

      .wb-notification-text {
        color: $pi-color-greyscale-darkest;
      }
    }

    &__contained {
        @extend .wb-soft-half--base;

        background-color: $notification-contained-background;
        width: auto;
    }

    &__pointerleft,
    &__pointerup {
        position: relative;
        background-color: $notification-pointer-background;
        display: block;

        .wb-icon_disc-info-new {
            color: $notification-pointer-icon-color;
        }
    }

    &__pointerleft:before,
    &__pointerup:before {
        position: absolute;
        content: '';
        display: inline-block;
        width: 0;
        height: 0;
    }

    &__pointerup:before {
        left: 10px;
        top: -8px;
        border-left: 10px solid $notification-pointerup-border-left;
        border-right: 10px solid $notification-pointerup-border-right;
        border-bottom: 8px solid $notification-pointerup-border-bottom;
    }

    &__pointerleft:before {
        left: -10px;
        top: 10px;
        border-top: 10px solid transparent;
        border-right: 10px solid $notification-pointerleft-border-right;
        border-bottom: 8px solid transparent;
    }
}
