%wb-radio--compact {
  .wb-radio__label {
    margin-bottom: $base-spacing--s;
  }

  .wb-radio__label__inner {
    color: $grayDark;
    margin-bottom: -1px;
    padding: 0 0 0 32px;

    .wb-radio__group--horizontal & { // Horizontal Version

      @include mq($from: tablet) {
        margin-bottom: 0;
        margin-right: -1px;
      }

      .wb-radio__label {
        margin-right: $base-spacing--s;

        &:last-child {
          margin-right: 0;
        }
      }
    }

    &:before {
      background-color: white;
      top: 0;
      left: 0;
    }

    &:after {
      top: 5px;
      left: 5px;
    }
  }

  input[type=radio] {
    // Outline
    &:focus+.wb-radio__label__inner {
      box-shadow: none;

      &:before {
        box-shadow: 0 0 1px 1px $pi-color-darkneutral-dark;
      }
    } 
    // Background color highlighted 
    &:checked+.wb-radio__label__inner:after{
      // DOT highlighted
      background-color: $pi-color-darkneutral-dark;
      border: solid 1px $pi-color-darkneutral-dark;
      top: 5px;
      left: 5px;
    }

    &:checked+.wb-radio__label__inner-text{
      // DOT highlighted
      font-weight: bold;
    }
  }
}