
.wb-radio {

  &__inner {

    input[type=radio] {
      // Hiden radio buttons and we can style as css
      @extend .hide;
    }

    label, .wb-radio__label {
      display: block;
      margin: 0;
    }

    .wb-radio__label__inner {
      display: block;
      padding: 11px 10px 10px 42px;
      position: relative;
      line-height: 1.5em;
      transition: all $standardTransitionSpeed ease-in-out;

      .wb-radio__label__inner__title,
      .wb-radio__label__inner-price {
        @include font-size($font-size--s, $line-height--s);
        font-weight: bold;
        display: inline-block;
      }

      &-info {
        @include font-size($font-size--xs, $line-height--xs);
      }

      &-link {
        @extend .wb-a;
        @include font-size($font-size--xs, $line-height--xs);
      }

      &:before {
        content: '';
        width: 22px;
        height: 22px;
        background-color: $pi-color-neutral-dark;
        border: solid 1px $grayLight;
        @include border-radius(22px);
        position: absolute;
        top: 10px;
        left: 10px;
      }

      &:after {
        content: '';
        @include border-radius(12px);
        width: 12px;
        height: 12px;
        position: absolute;
        top: 15px;
        left: 15px;
        opacity: 0;
        border: 1px solid transparent;
        transition: all $standardTransitionSpeed ease-in-out;
      }

      p, span, em, a {
        display: block;
        margin-bottom: 2px;
        &:last-child {
          margin-bottom: 0;
        }
      }
      em {
        margin-top: 6px;
        p { // :( 
          &:first-child {
            margin-top: 0;
          }
        }
      }
      a {
        margin-top: 12px;
        + a {
          margin-top: 2px;
        }
      }
    }

    .wb-radio__label__rate-card {
      display: flex;
      justify-content: space-between;

      .wb-radio__text-container,
      .wb-radio__label__inner__title,
      .wb-radio__label__inner__info {
        display: block;
      }

      .wb-radio__label__inner__title {
        @include font-size($font-size--base, $line-height--xs);
      }

      .wb-radio__label__inner__info {
        @include font-size($font-size--xs, $line-height--xs);
        font-weight: lighter;
      }

      .wb-radio__price-container {
        align-self: center;
        @include font-size($font-size--m, $font-size--m);
        font-weight: bold;
      }
    }

    &__expandable {
      .js & {
        display: block;
        overflow: hidden;
        height: 0;
      }
    }

    // Hover, Focus, & Checked CSS
    input[type=radio] {
      // Outline
      &:focus + .wb-radio__label__inner {
        // Outline / focus state
        box-shadow: 0 0 0 1px $pi-color-darkneutral-dark;
      }
      // Background color highlighted
      &:checked + .wb-radio__label__inner {
        
        // DOT highlighted
        &:after {
          background-color: $grayLight;
          border-color: $grayLight;
          opacity: 1;
        }

        .wb-radio__inner__expandable {
          height: auto;
        }
      }
    }

    .wb-form-item--clean & {
      @extend %wb-radio--clean !optional;
    }
    .wb-form-item--yellow & {
      @extend %wb-radio--yellow !optional;
    }
    .wb-form-item--compact & {
      @extend %wb-radio--compact !optional;
    }
  }

  &__group {
    list-style: none;
    margin: 0;
    padding: 0;
    
    // Style overwrites for horizontal
    &--horizontal {
      width: 280px;
      @media #{$breakpoint-sm} {
        width: auto;
        display: inline-block;
      }
      @extend .clearfix;
      .wb-radio__inner {
        @media #{$breakpoint-sm} {
          float:left;
        }
      }
      
    }
  }
}


// DEPRICATED - DIE DIE DIE
.wb-radio {
  list-style: none;
  @extend %wb-base-font-regular;
  position: relative;
  font-size: $font-size--s;
  padding: 0;
  margin: 0;

  &--inner,
  &--input {
    display:table;
  }

  &--label {
    display:table-cell;
    vertical-align:middle;
  }

  input[type=radio] {
    margin-right: 8px;
    width: $base-spacing--l;
    height: $base-spacing--l;
    padding: 0;
  }

  li {
    line-height: 15px;
  }

  //Theme overrides
  .wb-form-item--clean & {
    @extend %wb-radio--clean !optional;
  }
}
