@mixin wb-form-item-input {
  input[type="text"],
  input[type="search"],
  input[type="tel"],
  input[type="password"],
  input[type="url"],
  input[type="email"],
  input[type="datetime"],
  input[type="date"],
  input[type="month"],
  input[type="week"],
  input[type="time"],
  input[type="datetime-local"],
  input[type="number"],
  input[type="file"],
  input[type="button"],
  textarea,
  .wb-input-style {
    @content;
  }
}

.wb-form-error {
  outline: 2px solid $input-outline-error;
}

%wb-form-item--input-style {
  -moz-appearance: none;
  -webkit-appearance: none; // Reset for iOS
  color: $input-form-item-color;
  font-size: $font-size--base;
  font-weight: normal;
  width: 100%;
  display: block;
  padding: 5px 10px;
  box-sizing: border-box;
  background-color: $input-form-item-background;
  font-family: inherit;
  font-style: italic;
  border: 1px solid $input-form-item-border;
  margin: 0;
  outline-offset: -2px;
  border-radius: 0;
  cursor: pointer;
  height: 38px;

  .wb-form-item--with-symbol & {
    padding-left: 2em;
  }

  @include placeholder {
    font-weight: normal;
    font-family: inherit;
    font-size: $font-size--base;
    color: $input-form-item-placeholder-color;
    font-style: italic;
  }


  &::-ms-reveal {
    display: none;
  }

  &::-ms-clear {
    display: none;
  }

  &.info {
    margin-bottom: 0;

    &.ng-invalid.ng-dirty, &.error-field {
      border-color: $input-form-item-info-dirty-border;
    }
  }

  &.ng-invalid, &.error-field {
    @extend .wb-form-error;

    outline: 2px solid $input-form-item-outline;
    font-style: normal;
    color: $input-form-item-invalid-color;

    &:focus {
      outline: 2px solid $input-form-item-invalid-focus-outline;
    }
  }

  &.ng-dirty {
    font-style: normal;
    color: $input-form-item-dirty-color;
  }

  &.wb-dark {
    font-style: normal;
    color: $input-form-item-dark-color;
  }

  &.wb-dark:disabled {
    color: $input-form-item-disabled;
  }

  &:focus {
    outline: 2px solid $input-form-item-focus-outline;
    -webkit-appearance: none;
    -webkit-box-shadow: none;
    box-shadow: none;
  }

  @extend .truncate;
}

.wb-form-item {
  @include wb-form-item-input {
    @extend %wb-form-item--input-style;
  }


  //Themes
  &--clean {
    @include wb-form-item-input {
      @extend %wb-form-item--input-clean !optional;
    }
  }
}
