.wb-heading {
  // Refered to in design as Hero, H0 or HS
  &--hero {
    @include font-size($hero, $line-height--xxl);
  }

  &--h1 {
    @include font-size($h1, $line-height--xxl);
  }

  &--h2 {
    @include font-size($h2, $line-height--xl);
  }

  &--h3 {
    @include font-size($h3, $line-height--l);
  }

  &--h4 {
    @include font-size($h4, $line-height--m);
  }

  &--h5 {
    @include font-size($h5, $line-height--xs);
  }

  &--h6 {
    @include font-size($h6, $line-height--s);
  }

  &--subtext {
    @include font-size($subtext, $line-height--subtext);
  }

  &--border-color {
    border-color: $pi-color-greyscale-lighter;
  }
}
