.wb-form-item {
  position: relative;

  @extend %wb-base-font-regular;

  input[type="number"] {
    -moz-appearance: textfield;

    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }

    &::-webkit-outer-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }

  label,
  &__label {
    @extend .font-size--s, .mb1, .inline-block;

    cursor: pointer;
  }

  .wb-form-item__field__button {
    position: absolute;
    padding: 0 5px;
    right: 5px;
    top: 5px;
    height: 28px;
    font-size: $font-size--base;
    pointer-events: none;
  }

  .wb-form-item__field {
    position: relative;

    .wb-icon_arrow-down,
    .wb-icon_hotel-calendar {
      position: absolute;
      padding: 0 5px;
      right: 5px;
      top: 5px;
      width: 30px;
      height: 28px;
      background-color: $form-item-icon-background;
      border-radius: 3px;
      cursor: pointer;
      pointer-events: none;
      font-size: $font-size--l;
      color: $form-item-icon-color;

      &-disabled {
        @extend .wb-icon_arrow-down;

        color: $pi-color-greyscale-lighter;
      }
    }

    .wb-icon_arrow-down:before {
      position: absolute;
      top: 3px;
      left: 4px;
    }

    .wb-icon_hotel-calendar {
      > svg {
        position: inherit;
        right: 0;
        height: 28px;
        width: 30px;
        left: 3px;
        top: 2px;

        path[fill] {
          fill: $form-item-icon-path;
        }

        path[fill]:last-child {
          fill: $form-item-icon-path-lastchild;
        }
      }
    }

    > i {
      @extend .wb-btn;

      @extend .wb-form-item__field__button;

      &:before {
        vertical-align: -2px;
      }
    }

    .wb-btn {
      @extend .wb-form-item__field__button;

      vertical-align: -2px;
    }

    input {
      cursor: pointer;
    }

    &__full {
      width: 240px;

      @media #{$breakpoint-md} {
        width: 300px;
      }
    }

    &__half {
      width: 112.5px;

      @media #{$breakpoint-md} {
        width: 142.5px;
      }
    }
  }

  &__symbol {
    position: absolute;
    top: 50%;
    margin-top: -0.6em;
    padding-left: 10px;
    font-size: $font-size--base;
  } //Themes
  &--clean {
    @extend %wb-form-item-style--clean !optional;

    &.fixed-size {
      width: 280px;

      &.contained {
        width: 240px;

        @media #{$breakpoint-sm} {
          width: 385px;
        }
      }

      @media #{$breakpoint-sm} {
        width: 385px;
      }
    }
  }
}

.ng-submitted .wb-form-item .ng-invalid,
.ng-submitted .wb-form-item .error-field {
  @extend .wb-form-error;
}
