.wb-pb-header {
    display: block;
    background-color: $progressbar-background;
    width: 100%;

    @include mq($from: desktop) {
        border-bottom: 2px solid $progressbar-bottom-border;
    }


    &__container {
        @include mq($from: tablet) {
            min-height: $header-pb-height;
        }

        padding: $base-spacing--s 7.5px;
        max-width: 960px;
        margin: auto;

        @include mq($from: desktop) {
            padding: $base-spacing--s $base-spacing--base;
        }
    }

    &__container__inner {
        text-align: center;

        @include mq($from: tablet) {
            text-align: left;
            display: inline-block;
        }
    }

    &__privacy-link {
        @include font-size($font-size--xs);

        color: $progressbar-privacy-link-color;
        display: block;
        margin: 0 0 $base-spacing--xs $base-spacing--xs;

        @include mq($from: tablet) {
            margin: $base-spacing--base 0 0 45px;
        }
    }

    &__logo {
        display: inline-block;

        @include mq($from: tablet) {
            display: block;
            transform: translateY(1rem);
            overflow: hidden;
            width: 160px;
        }
    }
}

.wb-pb-booking-nav {
    @include mq($from: tablet) {
        display: inline-block;
        transform: translateY(1.2rem);
        position: absolute;
        right: 0;
    }


    @include mq($from: tabletLarge) {
        right: 12px;
    }


    @include mq($from: desktop) {
        position: static;
        float: right;
    }


    &__multi-steps {
        @extend .wb-flush--base, .wb-hard--base;
        display: flex;
        justify-content: center;

        &.clearfix {
            li {
                @include font-size($font-size--xs);
                position: relative;
                text-align: center;
                display: inline-block;
                color: $progressbar-list-color;
                width: 60px;

                @include mq($from: tablet) {
                    width: 80px;
                }


                @include mq($from: tabletLarge) {
                    width: 110px;
                }

                &::after {
                    @include font-size($font-size--xl);

                    @extend %wb-base-font-bold;

                    content: '..';
                    speak: none;
                    left: 48px;
                    position: absolute;
                    top: 3px;
                    height: 7px;
                    margin: 0;
                    color: $progressbar-dot-color;
                    line-height: 0;
                    letter-spacing: 3px;

                    @include mq($from: tablet) {
                        left: 62px;
                        top: 6px;
                        content: '...';
                    }


                    @include mq($from: tabletLarge) {
                        position: absolute;
                        left: 76px;
                        content: '.....';
                    }


                    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                        /* IE10+ CSS styles go here */
                        letter-spacing: 5px;
                    }
                }

                &:last-of-type::after {
                    display: none;
                }

                &:last-child {
                    @include mq($from: tablet) {
                        width: $base-spacing--xl;
                        margin-right: 7.5px !important;
                        margin-left: 25px;
                    }


                    @include mq($from: tabletLarge) {
                        width: 72px;
                        margin-left: 18px;
                    }

                }

                .wb-pb-booking-nav__step-indicator {
                    @extend %wb-base-font-bold;

                    @include font-size($font-size--xs, false);

                    color: $progressbar-step-indicator-color;
                    background-color: $progressbar-step-indicator-background;
                    border-radius: 50%;
                    width: 24px;
                    height: 24px;
                    display: block;
                    margin: auto;

                    @include mq($from: tablet) {
                        @include font-size($font-size--base, false);

                        width: $base-spacing--xl;
                        height: $base-spacing--xl;
                    }


                    .wb-pb-hidden-when-complete {
                        position: relative;
                        top: 4px;
                    }
                }

                &.is-active {
                    color: $progressbar-step-indicator-label-color-active;

                    .wb-pb-booking-nav__step-indicator {
                        color: $progressbar-step-indicator-color-active;
                        background-color: $progressbar-step-indicator-background-active;
                    }
                }

                &.is-completed {
                    .wb-pb-booking-nav__step-indicator {
                        @extend .wb-icon_tick;

                        background-color: $progressbar-step-indicator-background-complete;
                        color: $progressbar-step-indicator-color-complete;
                        font-family: 'wb-icons';

                        &:before {
                            position: relative;
                            top: 7px;
                        }
                    }

                    .wb-pb-hidden-when-complete {
                        display: none;
                    }
                }
            }

            &.steps4 li {
                &:last-child {
                    @include mq($from: tabletLarge) {
                        width: 92px;
                        margin-left: 8px;
                    }
                }
            }

            &.steps5 li {
                width: 55px;

                @include mq($from: tablet) {
                    width: 75px;
                }


                @include mq($from: tabletLarge) {
                    width: 104px;
                }


                &:after {
                    left: 43px;

                    @include mq($from: tablet) {
                        left: 55px;
                    }


                    @include mq($from: tabletLarge) {
                        left: 69px;
                    }
                }

                &:last-child {
                    @include mq($from: tablet) {
                        width: $base-spacing--xl;
                        margin-left: 23px;
                    }


                    @include mq($from: tabletLarge) {
                        width: 80px;
                        margin-left: 12px;
                    }
                }
            }
        }

        .wb-pb-booking-nav__step-name {
            white-space: nowrap;
            overflow-x: auto;
            overflow-y: hidden;
            padding-top: 4px;
            display: inline-block;

            @include mq($until: tabletLarge) {
                display: none;
            }
        }
    }
}

.html-ie9 .wb-pb-booking-nav {
    &__multi-steps {
        li {
            &::after {
                letter-spacing: 5px;
                left: 68%;
            }
        }
    }
}

/* Microsoft Edge Browser 12+ (All) - @supports method */

@supports (-ms-ime-align: auto) {
    .wb-pb-booking-nav {
        &__multi-steps {
            li {
                &::after {
                    letter-spacing: 9px;
                }
            }
        }
    }
}