%wb-radio--clean {
  &:first-child {
    .wb-radio__label__inner {
      border-radius: $standardBorderRadius $standardBorderRadius 0 0;
      .wb-radio__group--horizontal &, .wb-radio__group--horizontal#{&} {
        @include mq($from: tablet) {
          border-radius: $standardBorderRadius 0 0 $standardBorderRadius;
        }
      }
    }
  }
  &:last-child {
    .wb-radio__label__inner {
      border-radius: 0 0 $standardBorderRadius $standardBorderRadius;
      .wb-radio__group--horizontal &, .wb-radio__group--horizontal#{&} {
        margin-bottom: 0;
        @include mq($from: tablet) {
          margin-right: 0;
          border-radius: 0 $standardBorderRadius $standardBorderRadius 0;
        }
      }
    }
  }
  // I guess it could happen
  &:last-child:first-child {
    .wb-radio__label__inner {
      border-radius: $standardBorderRadius; 
    }
  }
  .wb-radio__label__inner {
    color: $grayDark;
    border: 1px solid $pi-color-darkneutral-dark;
    margin-bottom: -1px;

    &--no-border {
      border-color: transparent; // No Border Version - keeps sizing
    }

    .wb-radio__group--horizontal & { // Horizontal Version
      @include mq($from: tablet) {
        margin-bottom: 0;
        margin-right: -1px;
      }
    }

    &:before {
      border: 1px solid $pi-color-darkneutral-dark;
    }
  }
  input[type=radio] {
    // Outline
    &:focus+.wb-radio__label__inner {
      box-shadow: 0 0 0 1px $pi-color-darkneutral-dark;
    } // Background color highlighted 
    &:checked+.wb-radio__label__inner {
      // DOT highlighted
      background: $pi-color-darkneutral-light;
      &--no-border {
        background-color: transparent; // No Border Version
        box-shadow: none;
      }
      &:after {
        background-color: $pi-color-darkneutral-dark;
        border: solid 1px $pi-color-darkneutral-dark;
      }
    }
  }
  
}