body {
    color: $body-color;
    font-size: medium;
    margin: 0 auto;
    min-width: 320px;
}

img {
    max-width: 100%;
    height: auto;
}

.wb-container {
    padding: 0 $base-spacing--s;
    max-width: 1140px;

    @extend .mx-auto;

    @include mq($from: tabletLarge) {
        padding: 0 $base-spacing--m;
    }
}

.wb-border-container {
    padding: $base-spacing--base;
    border: 1px solid $pi-color-greyscale-light;
}

.md-col-clear {
    @include mq($until: tabletLarge) {
        clear: left;
    }
}
